import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { MatToolbarModule } from '@angular/material/toolbar';
import { NgClass, NgForOf, NgIf, NgTemplateOutlet } from '@angular/common';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { coerceBooleanProperty } from '@angular/cdk/coercion';
import { DscHeaderButtonData } from './shared/dsc-header-button-data';
import { DscBadgeDirective } from 'sidsc-components/dsc-badge';
import { DscButtonHeaderComponent } from 'sidsc-components/dsc-button-header';

export type AccessLevel =  'publico' | 'confidencial';

export type AccessModifier =  'interno' | 'externo';

@Component({
  selector: 'dsc-header',
  standalone: true,
  encapsulation: ViewEncapsulation.None,
  imports: [NgIf, NgClass, MatToolbarModule, MatIconModule, MatButtonModule, NgTemplateOutlet, DscButtonHeaderComponent, DscBadgeDirective, NgForOf],
  templateUrl: './dsc-header.component.html',
  styleUrls: ['./dsc-header.component.scss']
})
export class DscHeaderComponent {
  @Input()
  accessModifier: AccessModifier = 'interno';

  @Input()
  accessLevel?: AccessLevel = 'publico';

  @Input()
  accessLevelDescription?: string;

  @Input()
  acronym?: string;

  @Input()
  label?: string;

  @Input()
  userName?: string;

  @Input()
  userWorkUnit?: string;

  @Input()
  additionalInfo?: string;

  @Input()
  headerButtons?: DscHeaderButtonData[];

  @Input()
  customClass: string | string[] | Set<string> | {[p: string]: any} | null | undefined;

  @Input()
  get toggleButton(): boolean {
    return this._toggleButton;
  }

  set toggleButton(value: boolean | string) {
    this._toggleButton = coerceBooleanProperty(value);
  }

  private _toggleButton = false;

  @Input()
  get toggleButtonLabel(): boolean {
    return this._toggleButtonLabel;
  }

  set toggleButtonLabel(value: boolean | string) {
    this._toggleButtonLabel = coerceBooleanProperty(value);
  }

  private _toggleButtonLabel = false;

  @Input()
  get toggleButtonRight(): boolean {
    return this._toggleButtonRight;
  }

  set toggleButtonRight(value: boolean | string) {
    this._toggleButtonRight = coerceBooleanProperty(value);
  }

  private _toggleButtonRight = false;

  @Input()
  get brandFull(): boolean {
    return this._brandFull;
  }

  set brandFull(value: boolean | string) {
    this._brandFull = coerceBooleanProperty(value);
  }

  private _brandFull = true;

  @Output() toggle: EventEmitter<void> = new EventEmitter();

  onToggle(): void {
    this.toggle.emit();
  }

  @Input() buttonClickHandlers: { [key: string]: () => void } = {};

  onButtonClick(button: DscHeaderButtonData): void {
    const handler = this.buttonClickHandlers[button.label];
    if (handler) {
      handler();
    }
  }
}
